.navbar {
    width: 100%;
    background-color: var(--brand-color-white);
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* box-shadow: 0 1px 2px rgba(0,0,0,0.5); */

    top: 0;
    position: fixed;
    transition: top 0.3s;
    border-bottom: 2px solid #dee0e3;
}

.navbar-image-wrap {
    display: flex;
    align-items: center;
    margin: 10px;
    max-height: 55px;
    height: 55px;
}

.navbar-image {
    max-width: 100%;
    max-height: 100%;
    /* margin: 10px;
    max-height: 55px; */
}

.App {
    font-family: 'Jost', Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    text-align: center;
    color: #202124;
    margin: 0 10px 0 10px;
}

h2 {
    margin-top: 0;
}

h1 {
    margin: 10px 0 10px 0;
}

h1, h2 {
    font-weight: 600;
}

.logo-image {
    width: 100%;
}

.logo-wrap {
    width: 100%;
}

@media screen and (min-width: 425px) {
    .App {
        margin: 0 30px 0 30px;
    }

    .logo-image {
        width: 50%;
        min-width: 350px;
    }
}

@media screen and (min-width: 768px) {

    .logo-image {
        width: 60%;
        min-width: 350px;
    }
}

@media screen and (min-width: 1440px) {

    h1 {
        font-size: 52px;
    }

    h2 {
        font-size: 35px;
    }
}